omment_list;\r\n            new_itemList.push(item);\r\n      }\r\n      this.items = new_itemList;\r\n      console.log(this.items);\r\n    },\r\n    sendComment(i,e){ \r\n      var input = $(e.target).parent().siblings().eq(0);\r\n      var val = input.val();\r\n      var vm = this;\r\n      console.log(val);\r\n      //提交评论到后台\r\n      var data = {\r\n        content:val,\r\n        share_id:vm.items[i].share.share_id,\r\n        sendComment_id:0\r\n      };\r\n      api.fnc.Comment(data);\r\n      //更新数据\r\n      var c_data = {\r\n        content:val,\r\n        user:vm.login_user,\r\n        replyTo:null\r\n      };\r\n      input.val= \"\";\r\n      this.items[i].comment.push(c_data);\r\n      this.items[i].commentFlag = false;\r\n      \r\n    },\r\n    sendReply(index,e,item_index){\r\n      var replyTo = this.items[item_index].comment[index].user;\r\n      console.log(replyTo);\r\n      var input = $(e.target).parent().siblings().eq(0);\r\n      var val = input.val();\r\n      var vm = this;\r\n      console.log(val);\r\n      var data = {\r\n        content:val,\r\n        share_id:vm.items[item_index].share.share_id,\r\n        sendComment_id:replyTo.user_id\r\n      }\r\n      api.fnc.Comment(data);\r\n      var r_data = {\r\n                user:vm.login_user,\r\n                replyTo:replyTo,\r\n                content:val\r\n              }\r\n      this.items[item_index].comment.push(r_data);\r\n      input.val(\"\");\r\n      $(e.target).parent().parent().hide();\r\n    },\r\n    reply(e){\r\n      $(e.target).siblings(\".reply_bar\").toggle();\r\n    },\r\n    toComment(index){\r\n      this.items[index].commentFlag= !this.items[index].commentFlag;\r\n    },\r\n    toLike(index){\r\n      var share_id = this.items[index].share.share_id;\r\n      var id = {\r\n        share_id:share_id\r\n      };\r\n      api.fnc.Like(id);\r\n      this.items[index].like.push(this.login_user);\r\n      this.items[index].had_like = true;\r\n    },\r\n    toCollect(index){\r\n      var share_id = this.items[index].share.share_id;\r\n      var id = {\r\n        share_id:share_id\r\n      };\r\n      api.fnc.Collect(id);\r\n      this.items[index].had_collect = true;\r\n    },\r\n    showDialog(item_index,pic_index){\r\n      this.showImg =  this.items[item_index].picList[pic_index].url;\r\n      this.show = true;\r\n    }\r\n  }\r\n}\r\n</script>\r\n<style type=\"text/css\">\r\n  .author {\r\n    background-color:#31b0d5;\r\n    margin-bottom: 10px; \r\n  }\r\n  .author a {\r\n    color: #fff;\r\n    cursor: pointer;\r\n    font-size: 1.5em;\r\n    line-height: 2em;\r\n    display: inline-block;\r\n  }\r\n  .author_head{\r\n    width: 30px;\r\n    height: 30px;\r\n    margin-right:8px;\r\n  }\r\n  .pic-show img {\r\n    width: \r\n  }\r\n  .comments{\r\n    padding: 10px 0;\r\n  }\r\n  .comment-list{\r\n    text-align: left;\r\n    margin:0;\r\n    padding: 5px 0;\r\n  }\r\n  .like{\r\n    text-align: left;\r\n  }\r\n  .like-num{\r\n    margin: 0 10px;\r\n    color: #31b0d5;\r\n    font-size: 2rem;\r\n  }\r\n  .panel-inner{\r\n    margin-bottom: 1.5em;\r\n    box-shadow: 0 0 5px #888888;\r\n  }\r\n  .reply_input,.comment_input{\r\n    line-height: 2em;\r\n  }\r\n  .submit_reply,.submit_comment{\r\n    background-color: #31b0d5;\r\n    color: #fff;\r\n    line-height: 2em;\r\n  }\r\n  .header-nav{\r\n    background-color: #e6e6e6;\r\n    line-height: 2.5em;\r\n  }\r\n  .active_tab{\r\n    color: #fff;\r\n    background-color: #31b0d5;\r\n    padding: 3px 10px;\r\n    border-radius: 5px;\r\n  }\r\n  .comment_btn{\r\n    display: inline-block;\r\n    width:25px;\r\n    height: 25px;\r\n    background: url('../logo/comment.png');\r\n    background-size: contain;\r\n  }\r\n  .had_like{\r\n    background: url('../logo/islike.png');\r\n  }\r\n  .had_collect{\r\n    background: url('../logo/iscollect.png');\r\n  }\r\n  .had_lock{\r\n    background: url('../logo/collect.png');\r\n  }\r\n  .no_like{\r\n    background: url('../logo/like.